import { Paper, Zoom } from '@mui/material'
import * as React from 'react'
import { AppContext, AppContextType } from '../contexts'
import { getTheme } from '../engine/theme'
import { IItemProps, IDeckItem } from '../interfaces'
import { seedItem } from '../engine'
import { DropTarget } from 'react-drag-drop-container'

export const CardTo: React.FC<IItemProps> = ({ id, onClick, selected }) => {  
  const { store, dispatch } = React.useContext(AppContext) as AppContextType
  const [isHover, setHover] = React.useState(false)
  const currentTheme = getTheme(store.themeIndex)
  const deckItem = store.tableState.deck?.Items.find(
    (item: IDeckItem) => item.id === id
  )
  const media = store.tableState.deck?.ToType.toString()
  const base = 'https://boomza.com/geo-quiz' + store.deckList?.[store.category].Prefix
  let innerHTML = <b>!ERROR:{id}</b>
  if (deckItem && media) {    
    switch (media) {
      case 'IMAGE':
        innerHTML = <img src={base + seedItem(deckItem.to)} alt={seedItem(deckItem.from)} width='90' height='50'/>
        break
      default: 
        innerHTML = <span>{seedItem(deckItem.to)}</span>
    }    
  }

  return (
    <DropTarget
      targetKey="hansom"
      dropData={id}
      onHit={(e: { [x: string]: string | number }) => {
        if (e['dragData'] === id) {
          dispatch({ type: 'PRE_MATCH', event: { key: 'hideId', value: id } })         
        }
        setHover(false)
      }}
      onDragEnter={() => setHover(true)}
      onDragLeave={() => setHover(false)}
      render={() => {
        return (
          <Zoom in={store.hideId !== id} timeout={{enter:500, exit:250}} onExited={() => dispatch({ type: 'MATCHED', event: { key: 'id', value: id } })}>
            <Paper
              onClick={() => onClick(id)}
              sx={{
                position: 'relative',
                fontSize: store.fontSize,
                textAlign: 'center',
                color: currentTheme.palette.text.primary,
                borderRadius: '2em',
                padding: '1em',
                width: '40vw',
                display: 'block',
                marginLeft: 'auto',
                borderStyle: 'solid',
                marginRight: 'auto',
                marginTop: '1em',
                transition: 'background-color .1s ease-in',
                backgroundColor:
                  selected || isHover
                    ? currentTheme.palette.background.default
                    : currentTheme.palette.background.paper
              }}
            >
              {innerHTML}
            </Paper>
          </Zoom>
        )
      }}
    />
  )
}

export default CardTo

